import React from 'react';
import { graphql, useStaticQuery, Link } from 'gatsby';
import Img from 'gatsby-image';

// Components
import Layout from '../components/Layout/Layout';

function PestsPage() {
	const data = useStaticQuery(graphql`
		{
			allContentfulBlog {
				edges {
					node {
						title
						slug
						img {
							fluid {
								...GatsbyContentfulFluid
							}
						}
						createdAt
					}
				}
			}
		}
	`);
	const res = data.allContentfulBlog.edges.map((each) => {
		return (
			<Link to={`/${each.node.slug}`}>
				<div style={{ width: '300px' }}>
					<div className="d-flex justify-content-center pb-2 mt-5">
						<Img fluid={each.node.img.fluid} alt="img" style={{ width: '120px' }} />
					</div>
					<h5 className="text-center mb-5">{each.node.title}</h5>
					<hr />
				</div>
			</Link>
		);
	});
	return (
		<Layout title="آفت ها">
			<div className="container mt-5">
				<h4 className="text-center">انواع آفت و زیان های آن ها</h4>
				<hr />
				<div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>{res}</div>
			</div>
		</Layout>
	);
}

export default PestsPage;
